@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:black;background-color:#f8f8f8}.header{border-bottom:solid 1px #ca2100;background-color:#db4c3f;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%}.header .logo{color:white}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto}.content{display:flex;max-width:922px;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:264px;height:100vh;padding-top:74px;border-right:1 px solid #f1f1f1;background-color:#f1f1f1}.sidebar .active{background-color:white}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div .icon{margin-right:10px}.tasks{background-color:white;height:100vh;width:calc(922px - 266px);padding:74px 20px 0px}.tasks .add-task-btn{cursor:pointer}.tasks .add-task-btn .plus{color:#db4b39;font-size:18px;margin-right:10px}.tasks .add-task-btn .add-task-text{color:#545454;font-size:14px}.tasks .add-task-dialog{margin-top:10px}.tasks .add-task-dialog input{width:100%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .add-task-actions-container{display:flex;flex-direction:row;justify-content:space-between}.tasks .add-task-actions-container .btns-container{padding:10px 0px}.tasks .add-task-actions-container .btns-container button{cursor:pointer;margin-right:5px;padding:10px 12px;border-radius:3px}.tasks .add-task-actions-container .btns-container .add-btn{color:white;background-color:#db4b39;border:1px solid #db4b39}.tasks .add-task-actions-container .btns-container .add-btn:disabled{background-color:gray;border:1px solid grey}.tasks .add-task-actions-container .btns-container .cancel-btn{background-color:#ddd;border:1px solid #ddd}.display-tasks{display:block}.display-tasks .task-display{display:flex;flex-direction:row;justify-content:space-between}#task-list{display:flex;flex-direction:row;justify-content:space-between;padding:10px 14px;border-bottom:2px solid #db4b39}#task-list #task-date{font-weight:lighter;color:grey}

